.grade {
    padding: 2em;
    margin: 0 auto;
    text-align: center;
    box-shadow: var(--shadow);
    transition: transform 0.2s linear;
}

.grade:hover {
    transform: translateY(-7px);
}

.grade__date {
    margin: 1.7em 0;
    font-weight: 500;
    font-size: 0.8rem;
    color: var(--clr-fg-alt);
}

.grade__title {
    margin-top: 1em;
    font-size: 1.1rem;
    /* font-weight: 600;
    font-size: 1.1rem; */
}