.work {
    padding: 0 0 2em 0;
    box-shadow: var(--shadow);
    transition: transform 0.2s linear, max-height 0.3s ease-in-out, clip-fade 0.4s ease-in-out;
    width: 100%;
    max-height: 19em;
    overflow: hidden;
}

.work-mask {
    padding: 2em 2em 0 2em;
    mask-image: linear-gradient(to bottom, #000 80%, transparent);
    margin: 0 auto;
    text-align: center;
    height: 100%;

}

.work:hover {
    transform: translateY(-7px);
    max-height: 100%;
}


.work-mask:hover {
    mask-image: none;
}

.work__date {
    margin: 1.7em 0;
    font-weight: 500;
    font-size: 0.8rem;
    color: var(--clr-fg-alt);
}

.work__title {
    margin-top: 1em;
    font-size: 1.1rem;
    font-weight: 500;
}

.work_description__grid {
    list-style-type: square;
    list-style-position: inside;
}

.work__detail {
    text-align: start;
    font-size: 0.95rem;
}

@keyframes clip-fade {
    0% {
        mask-position: 100% 0%;
    }

    100% {
        mask-position: 0% 0%;
    }
}