.about {
    flex-direction: column;
    margin-top: 3em;
}

.about__name {
    color: var(--clr-primary);
}

.about__role {
    margin-top: 1.2em;
}

.about__desc {
    font-size: 1rem;
    max-width: 600px;
}

.about__desc,
.about__contact {
    margin-top: 2.4em;
}

.about .link--icon {
    margin-right: 0.8em;
}

.about .btn--outline {
    margin-right: 1em;
}

.about__avatar {
    width: 9em !important;
    height: 9em !important;
    border-radius: 50%;
    overflow: hidden;
    margin-bottom: 1em;
    align-self: center;
}

@media (max-width: 600px) {
    .app .about {
        align-items: flex-start;
        margin-top: 2em;
    }

    .about__avatar {
        width: 5em !important;
        height: 5em !important;
    }
}